import React, { Component } from 'react';
import { ReactComponent as Tables } from './tables.svg';
import Modal from '../Helpers/Modal';
import '../../styles/Map.css';

export default class MapField extends Component {


 state={ 
   isModalOpen : false
 }


  componentDidMount() {

    const svgDoc = document.getElementById("tableShapes").childNodes[1];

    const tables = svgDoc.querySelectorAll('[id^=t]');

    tables.forEach(table => {
      table.style.cursor = "pointer";

      table.addEventListener('mouseover', function () {
          this.style.fill = "var(--tableHover)";
      });
      table.addEventListener('mouseout', function () {
        this.style.fill = "";
      })

      table.addEventListener('click', function () {      // reserded tables onClick

        this.setState({
          isModalOpen: true
        })
        this.style.cssText = "pointer-events: none; cursor: default"
        this.setAttribute('filter', 'url(#grayscale)');
        this.innerHTML += '<filter id="grayscale"><feColorMatrix type="matrix"'+
        'values="0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0"/></filter>';
        
        // table.innerHTML += '<path class="st8" d="M-1369.4-922.5c0,49.7,90,150,90,150s90-100.3,90-150s-40.3-90-90-90 C-1329.1-1012.5-1369.4-972.2-1369.4-922.5z"></path>';
        // let text = document.createElementNS("http://www.w3.org/2000/svg", "text");
        // let name = document.createTextNode("Екатерина");
        // let hours = document.createTextNode("17:00");
        // text.appendChild(name);
        // text.appendChild(hours);
        // text.setAttribute("x", -1339.397);
        // text.setAttribute("y", -907.4561);
        // text.setAttribute("font-size", '25px');
        // table.appendChild(text);
      })
    });
  }
  render() {
    return (
       <>
       <div className="map-wrapper">
          <Tables id="tableShapes"/>
        </div>
       
         <Modal
         title="Corp Actions"
         width={900}
         ></Modal>
        
        </>
    )
  }
}
